
/*#region General*/
html{
  box-sizing:border-box;
  padding:0;
  margin:0;
}
body{
  box-sizing:border-box;
  padding:0;
  margin:0;
  display:flex;
  flex-direction:column;
}
@font-face{
  font-family:"Myriad Thin";
  src:url("./assets/fonts/01.ttf") format("truetype");
}
@font-face{
  font-family:"Myriad Ultra Light";
  src:url("./assets/fonts/02.ttf") format("truetype");
}
/*#endregion General*/

/*#region Section1*/
#section1{
  position:relative;
  height:100vh;
  width:100%;
  overflow:hidden;
}
#section1 video{
  height:100%;
  width:100%;
  object-fit:cover;
  object-position:0% bottom;
}
#section1 .text{
  position:absolute;
  top:34%;
  left:135px;
}
#section1 .text .logo{
  color:#b1986a;
  font-size:3rem;
}
#section1 .text h1{
  font-family:"Myriad Ultra Light";
  font-size:50px;
  font-weight:300;
  color:#b1986a;
}
#section1 .text .box{
  display:flex;
  /*Esta altura separa a los dos links
   * funciona como un margin*/
  height:1.7rem;

}
#section1 .text .box a{
  display:flex;
  align-content:flex-end;
  align-items:flex-end;
  font-family:"Myriad Thin";
  font-size:17px;
  opacity:0.8;
  text-decoration:none;
  text-size-adjust:100%;
  color:#fff;
  margin:0;
  transition:all .05s ease;
  z-index:99999;
}
#section1 .text .box .icon img{
  margin:.9rem 0 0 .2rem;
}
#section1 #menu-btn{
  display:flex;
  justify-content:center;
  align-items:center;
  position:fixed;
  height:43px;
  width:50px;
  top:30px;
  right:35px;
  z-index:999999;

}
#section1 #menu-btn .menu-lines{
  display:flex;
  justify-content:center;
  align-items:center;
  position:relative;
  height:100%;
  width:100%;
}
#section1 #menu-btn .menu-lines .line{
  position:absolute;
  height:2px;
  width:34px;
  background:#b1986a;
}
#section1 #menu-btn .menu-lines .line1{
  position:absolute;
  top:10px;
  height:2px;
  width:34px;
}
#section1 #menu-btn .menu-lines .line2{
  top:17px;
}
#section1 #menu-btn .menu-lines .line3{
  top:24px;
}
/*#endregion Section1*/

/*#region Navbar Button*/
#site-wrapper #menu-btn{
  display:flex;
  justify-content:center;
  align-items:center;
  position:fixed;
  top:25px;
  right:30px;
  height:50px;
  width:50px;
  cursor:pointer;

  overflow:hidden;
  z-index:999999;
}
#site-wrapper .menu-lines{
  display:flex;
  justify-content:center;
  align-items:center;
  position:relative;
  height:80%;
  width:100%;
}
#section1 .menu-lines .line{
  position:absolute;
  height:2px;
  width:34px;
  background:#b1986a;
  transition:all .5s ease-in-out;
  /*
  transform-origin:left center;
  */
}
#section1 .menu-lines .line1{
  top:10px;
}
#section1 .menu-lines .line2{
  top:17px;
}
#section1 .menu-lines .line3{
  top:24px;
}
#section1 #menu-btn.active .menu-lines{
  background:none;
}
#section1 #menu-btn.active .line1{
  /*
    transform:rotate(45deg) translate(28px,10%);
  */
  transform: translate3d(0,7px,0) rotate(45deg);
  /*
  top:-3px;
  */
}
#section1 #menu-btn.active .line2{
  background:none;
}
#menu-btn.active .line3{
  /*
  transform:rotate(-45deg) translate(28px,10%);
  */
  transform: translate3d(0,-7px,0) rotate(-45deg);
  /*
  top:39px;
  */
}
#section1 #menu-btn.hovered.no-active .line1{
/*
  transform:rotate(45deg) translate(28px,10%);
*/
  transform: translate3d(0,-2px,0);
  /*
  top:-3px;
  */
}
#section1 #menu-btn.hovered.no-active .line3{
  /*
  transform:rotate(-45deg) translate(28px,10%);
  */
  transform: translate3d(0,2px,0);
  /*
  top:39px;
  */
}
/*#endregion Navbar Button*/

/*#region Section2*/
/*There are a bug when using 
 * the img element inside the
 * box component.A white border
 * appears.
 * But at the same time there are
 * a class name conflict with 
 * section2 Link component when I try
 * to solve it.
 * TODO: fix clas name conflict or
 * change the img element from the
 * box component.
 * */
#section2{
  height:1260px;
  width:100%;
}
#section2 .container{
  display:grid;
  grid-template-columns:1fr 1fr 1fr;
  grid-template-rows:repeat(3,1fr);
  height:100%;
  width:100%;
} 
#section2 .box{
  position:relative;
  background:yellow;
  min-height:0%;
  min-width:0%;
  overflow:hidden;
} 
#section2 .box .text p{
  overflow:hidden;
  font-family:"Myriad Ultra Light";
  font-size:40px;
  color:#767676;
} 
#section2 .box .text h3{
  font-family:"Myriad Ultra Light";
  font-size:25px;
  color:#b1986a;
  font-weight:200;
} 
#section2 .box .text .icon div{
  height:2px;
  width:20px;
  background-color:#b1986a;
}      
#section2 .box1{
  grid-column:1/2;
  grid-row:;
}           
#section2 .box2{
  grid-column:2/4;
  grid-row:;
}     
#section2 .box3{
  grid-column:;
  grid-row:;
  background:#B1986A;
}     
#section2 .box4{
  grid-column:;
  grid-row:;
}     
#section2 .box5{
  grid-column:;
  grid-row:;
}     
#section2 .box6{
  grid-column:1/3;
  grid-row:;
}     
#section2 .box7{
  grid-column:;
  grid-row:;
  background:rgba(0,0,0,0.9);
} 
#section2 .box img{
  height:100%;
  width:100%;
  overflow:hidden;
  object-fit:cover;
  transition:all .6s ease-in-out;
}      
/*Remove unwanted white border from img tag
 * TODO: stop using img tags instead use div*/
#section2 .box7 img{
  height:0;
  width:0;
} 
#section2 .box .hovereffect{
  position:relative;
  height:100%;
  width:100%;
  transition:all .6s ease-in-out;
}  
#section2 .box .overlay{
  display:flex;
  justify-content:center;
  align-items:center;
  position:absolute;
  top:0;
  left:0;
  height:100%;
  width:100%;
  padding:0 4rem 0 0;
  box-sizing:border-box;
  transform:translate3d(-100%,0,0);
  transition:all .6s ease-in-out;
  overflow:hidden;
  z-index:999999;
}
#section2 .box .hovereffect:before{
  content:"";
  position:absolute;
  top:0;
  left:0;
  background:rgba(0,0,0,0.5);
  height:100%;
  width:100%;
  z-index:999;
  transition:all .3s ease-in-out;
}
/*Animation*/
#section2 .box .hovereffect:hover .overlay{
  transform:translate3d(0,0,0);
}
#section2 .box .hovereffect:hover img{
  transform:scale3d(1.1,1.1,1.1);
}
#section2 .box:hover .hovereffect:before{
  background:rgba(0,0,0,0.7);
}
/*Text box*/
#section2 .box p{
}
#section2 .box4 p{
  text-transform:capitalize;
}
#section2 .box3{
  display:flex;
  flex-direction:column;
  justify-content:center;
  padding:3rem;
  box-sizing:border-box;
}
#section2 .box3 p{
  font-family:"Myriad Ultra Light";
  font-size:17px;
  font-weight:700;
  line-height:1.3;
  color:rgba(255,255,255,0.5);
  text-align:center;
}
#section2 .box3 .link{
  display:flex;
  justify-content:center;
  align-items:center;
  z-index:9999;
}
#section2 .box3 a{
  font-family:"Myriad Thin";
  font-size:17px;
  color:rgba(255,255,255,0.8);
  text-decoration:none;
  text-align:center;
}
#section2 .box3 .link .icon{
  margin:0 0 .1rem .2rem;
}

/*#endregion Section2*/

/*#region Navbar*/
#navbar{
  display:none;
  flex-direction:column;
  justify-content:center;
  align-items:center;
  position:fixed;
  height:100%;
  width:100%;
  top:0;
  left:0;
  background:#000;
  opacity:0;
  transition:all .3s ease-in-out;
  z-index:99999;
}
#navbar.show{
  display:flex;
  opacity:1;
}
#navbar ul{
  display:flex;
  flex-direction:column;
  justify-content:center;
  align-items:center;
  width:100%;
  padding:0;
  margin:4rem 0 0 0;
  list-style:none;
}
#navbar ul li{
  margin:.3rem 0 0 0;
}
#navbar ul li>a{
  text-decoration:none;
  font-family:"Myriad Ultra Light";
  font-size:40px;
  text-transform:uppercase;
  color:#b1986a;
}
#navbar .logo{
  font-family:"Myriad Ultra Light";
  font-size:40px;
  text-transform:uppercase;
  text-decoration:none;
  text-align:center;
  color:#b1986a;
}

/*#endregion Navbar*/

/*#region Section3*/
